"use client"
import { useState } from "react"

export default function Deliveries() {
  const [date, setDate] = useState("")
  const [quantity, setQuantity] = useState("")

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    alert("Delivery recorded âœ…")
    setDate("")
    setQuantity("")
  }

  return (
    <div>
      <h1 className="text-2xl font-bold mb-4">Record Fuel Delivery</h1>
      <form
        onSubmit={handleSubmit}
        className="bg-white p-6 rounded-xl shadow-md max-w-md space-y-4"
        aria-label="Fuel delivery form"
      >
        <div>
          <label className="block font-semibold mb-1">Date</label>
          <input
            type="date"
            value={date}
            onChange={(e) => setDate(e.target.value)}
            className="w-full p-2 border rounded-lg focus:ring-2 focus:ring-teal-500"
            required
          />
        </div>
        <div>
          <label className="block font-semibold mb-1">Quantity (Liters)</label>
          <input
            type="number"
            value={quantity}
            onChange={(e) => setQuantity(e.target.value)}
            className="w-full p-2 border rounded-lg focus:ring-2 focus:ring-teal-500"
            required
          />
        </div>
        <button
          type="submit"
          className="w-full bg-teal-600 hover:bg-teal-700 text-white font-semibold py-2 rounded-lg"
        >
          Save Delivery
        </button>
      </form>
    </div>
  )
}
